/* General settings */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #121212;
  color: #e0e0e0;
  transition: background-color 0.5s ease, color 0.5s ease;
}

.App {
  text-align: center;
}

/* Header Navigation */
.App-header {
  background-color: #1c1c1c;
  padding: 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5);
}

.App-header nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

.App-header nav ul li {
  margin: 0 20px;
}

.App-header nav ul li a {
  text-decoration: none;
  color: #e0e0e0;
  font-weight: bold;
  font-size: 1.2rem;
  position: relative;
  transition: color 0.3s ease, transform 0.3s ease;
}

.App-header nav ul li a::before {
  content: '';
  position: absolute;
  width: 0%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: #61dafb;
  transition: width 0.4s ease;
}

.App-header nav ul li a:hover::before {
  width: 100%;
}

.App-header nav ul li a:hover {
  color: #61dafb;
  transform: translateY(-3px);
}

/* Main content area */
main {
  padding: 120px 20px;
  transition: background-color 0.5s ease, color 0.5s ease;
}

h1, h2 {
  font-size: .5rem;
  color: #ffffff;
  letter-spacing: 1px;
  transition: transform 0.5s ease;
}

h2 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}

/* Left-aligned sections */
.resume, .experience, .projects {
  text-align: left;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

/* About Page */
.intro h1 {
  font-size: 1.4rem;
  color: #61dafb;
}

.intro h2 {
  font-size: 3rem;
  color: #e0e0e0;
}

.intro .work p {
  font-size: 1.5rem;
  font-style: italic;
  color: #c0c0c0;
  margin-bottom: 20px;
  animation: fadeInUp 1s ease-in-out;
}

.intro .about p {
  font-size: 1.2rem;
  line-height: 1.6;
  color: #b0b0b0;
  max-width: 800px;
  margin: 0 auto;
  opacity: 0;
  animation: fadeIn 2s ease-in forwards;
}

/* Experience Page */
.experience h2, .experience h3 {
  color: #ffffff;
  animation: fadeIn 1s ease-in;
}

.experience ul {
  padding-left: 20px;
  list-style: none;
  color: #b0b0b0;
}

.experience ul li {
  margin-bottom: 10px;
  opacity: 0;
  animation: fadeInUp 1.2s ease forwards;
}

/* Resume Page */
.resume h2, .resume h3 {
  color: #ffffff;
}

.resume p {
  font-size: 1.1rem;
  color: #b0b0b0;
}

.resume ul {
  padding-left: 20px;
  list-style: none;
  color: #b0b0b0;
}

.resume ul li {
  margin-bottom: 10px;
}

/* Projects Page */
.projects h2, .projects h3 {
  color: #ffffff;
}

.projects ul {
  padding-left: 20px;
  list-style: none;
  color: #b0b0b0;
}

.projects ul li {
  margin-bottom: 20px;
  opacity: 0;
  animation: fadeInUp 1.2s ease forwards;
}

/* Contact Page */
.contact h2 {
  font-size: 2.5rem;
  color: #ffffff;
}

.contact p {
  font-size: 1.5rem;
  color: #b0b0b0;
}

.contact a {
  text-decoration: none;
  color: #61dafb;
  transition: color 0.3s ease;
}

.contact a:hover {
  color: #ffffff;
}

/* Animation Effects */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Button-like hover effect for links */
.App-header nav ul li a:hover {
  background-color: #1c1c1c;
  color: #61dafb;
  padding: 5px 10px;
  border-radius: 5px;
}
